.ourmodels_res{
    padding-top:120px;
    background-color: #111111;
}

.ourmodels_header_res{
    width:85%;
    margin: auto;
}

.ourmodels_header_res h2{
    font-size: 27px;
    font-weight: lighter;
    color:#fff;
}

.ourmodels_header_res p{
    font-size:15.5px;
    font-weight: bold;
    color:#bebebe;
    margin:20px 0px 70px 0px;
}

.ourmodels_header_res button{
    display:flex;
    width:100%;
    justify-content: center;
    border: none;
    color:#808080;
    background-color: transparent;
}

.ourmodels_header_res button div{
    font-size:16px;
    padding:0px 32px 15px 32px;
    border-bottom: 1px solid #808080;
}

.ourmodels_header_res button div:first-child{
    color:#fff;
    border-bottom: 1px solid #fff;
}

.ourmodels_img_layout_res{
    width:100%;
    margin:auto;
    overflow: hidden;
}

.ourmodels_img_container_res{
    display:flex;
    transform: translateX(0px);
}

.ourmodes_img_item_res{
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    width:100%;
    margin: auto;
    flex-shrink : 0;
    padding:50px 0px 50px 0px;
    background-color: rgb(21, 21, 21);
}

.ourmodes_img_item_res img{
    width:85%;
    margin: auto;
    padding:50px 0px 50px 0px;
}

.ourmodes_img_item_res h3{
    width:85%;
    margin: auto;
    font-size:23px;
    font-weight: lighter;
    color:#bbb;
    padding-bottom:10px;
}

.ourmodes_img_item_res p{
    width:85%;
    height:0px;
    margin: auto;
    font-size:13px;
    color:#bbb;
}

.ourmodes_img_item_res button{
    width:85%;
    margin: auto;
    padding:15px 0px 15px 0px;
    font-size: 17px;
    font-weight: bold;
    border: 1px solid #fff;
    color:#fff;
    background-color: transparent;
}

.ourmodels_btn_res{
    text-align: center;
    padding:30px 0px 50px 0px;
}

.ourmodels_btn_res button{
    width:7px;
    height:7px;
    padding:0px;
    margin:0px 8px 0px 8px;
    border-radius: 50%;
    border:none;
    background-color: #808388;
    cursor: pointer;
}

.ourmodels_btn_res button:first-child{
    outline: 2px solid #fff;
    background-color: transparent;
}

.ourmodels_icon_res{
    padding-bottom:70px;
}

.ourmodels_icon_item_res{
    display:flex;
    align-items: center;
    color:#fff;
    padding:20px 0px 20px 0px;
    border-bottom: 1px solid #808388;
}

.ourmodels_icon_item_res:last-child{
    border: none;
}

.itme_container_res{
    display:flex;
    align-items: center;
    width:85%;
    margin: auto;
}

.itme_container_res img{
    width:9%;
    margin-right:20px;
}

.itme_container_res p{
    font-size:15px;
    font-weight: bold;
}