
.footer_list{
    display: flex;
    align-items: center;
    justify-content: space-between;
    color:#fff;
    padding:20px 35px 20px 35px;
    border-bottom: 1px solid #1e1e1e;
    background-color: #151515;
}

.footer_list div{
    font-size:13px;
    font-weight: bold;
}

.footer_about_res{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #111111;
    color:#bbb;
    padding:40px 0px 40px 0px;
}

.footer_about_res div{
    padding:10px 0px 10px 0px;
}

.footer_about_res div span{
    font-size:13px;
    padding:0px 10px 0px 10px;
    border-right: 1px solid #bbb;
}

.footer_sns_res ul{
    display:flex;
    justify-content: center;
    padding-top:10px;
}

.footer_sns_res ul li {
    color:#fff;
    font-size:30px;
    padding:0px 20px 0px 20px;
}

.footer_more_res ul li{
    display: flex;
    align-items: center;
    justify-content: space-between;
    color:#bbb;
    padding:20px 35px 20px 35px;
    border-bottom: 1px solid #1e1e1e;
    background-color: #151515;
}

.footer_more_res ul li div{
    font-size:13px;
    font-weight: bold;
}

.footer_copy_res{
    display:flex;
    flex-direction: column;
    align-items: center;
    background-color: #111111;
    padding:50px 0px 50px 0px;
}

.footer_copy_res img{
    width:30%;
}

.footer_copy_res p{
    font-size:13px;
    color:#fff;
    text-align: center;
    margin-top:10px;
}