/*네비게이션 바*/
.nav_bar{position: fixed;
    display:flex;
    justify-content: center;
    align-items: center;
    width:100%;
    height:70px;
    color:#bebebe;
    background-color: #111111;
    z-index: 999;
}

.nav_bar ul{
    display:flex;
}

.logo{
    width:12%;
}

.logo img{
    width:128px;
}

.main_nav{
    width:60%;
}

.main_nav ul li{
    font-size:15px;
    font-weight: bold;
    padding:0px 28px 0px 28px;
}


.main_nav ul li:first-child{
    padding-left:0px;
}

.sub_nav ul li{
    font-size:13px;
    font-weight: bold;
    padding:0px 20px 0px 20px;
}

.sub_nav ul li svg{
    font-size:18px;
    color:#fff;
}


.sub_nav ul li:first-child{
    padding-left:0px;
}

.sub_nav ul li:last-child{
    border-left: 1px solid #3b3b3b;
}


/*이미지 슬라이드 배너*/
.img_banner{
    position: relative;
    overflow: hidden;
}

.img_container{
    display:flex;
}

.img_item{
    position: relative;
    width:100%;
    overflow: hidden;
    flex-shrink:0;
}

.img_text_header{
    position: absolute;
    top:10%;
    left:18%;
    color:#fff;
    z-index: 1;
}

.img_text_header h3{
    font-size:23px;
    font-weight:lighter;
}

.img_text_header h1{
    font-size:75px;
    font-weight:lighter;
    margin:5px 0px 15px 0px;
}

.img_text_header h2{
    font-size:24px;
    font-weight:lighter;
}

.img_item img{
    height: 100%;
    transform: translateX(-320px);
}

.img_text_p{
    position: absolute;
    bottom:24%;
    left:18%;
    display:flex;
    align-items: center;
    color:#fff;
    cursor: pointer;
}

.img_text_p div{
    display:flex;
    align-items: center;
}

.img_text_p div:first-child{
    margin-right:60px;
}

.img_text_p p{
    margin-right: 7px;
}

.img_side_linear{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height:80px;
}

.img_side_linear hr{
    width:342px;
    height: 2px;
    background-color: #fff;
}

.img_btn_container{
    position:absolute;
    bottom:15%;
    left:50%;
    transform: translateX(-50%);
    display:flex;
    align-items: center;
}

.img_btn_container button{
    width:11px;
    height:11px;
    padding:0px;
    margin:0px 10px 0px 10px;
    border-radius: 50%;
    border:none;
    background-color: #808388;
    cursor: pointer;
}

.img_btn_container button:first-child{
    border:2px solid #fff;
    background-color: transparent;
}

.img_btn_container svg{
    margin-left:10px;
    color:#fff;
    cursor: pointer;
}
