.content_res{
    margin-top:100px;
}

.content_res h2{
    text-align: center;
    font-size:27px;
    margin-bottom:30px;
}

.content_item_res {
    width:85%;
    margin: auto;
    margin-bottom:50px;
}

.content_item_res img{
    width:100%;
}

.content_item_res h5{
    font-size:17px;
    margin-top:15px;
}

.content_item_res p{
    font-size:16px;
    padding:15px 0px 15px 0px;
}

.content_item_res div{
    display:flex;
    align-items: center;
}

.content_item_res div a{
    margin-right:5px;
    padding-bottom: 3px;
    border-bottom: 1px solid black;
}