.awards_slide{
    position:relative;
    height:940px;
    overflow:hidden;
}

.awards_title{
    width: 70%;
    margin:auto;
}

.awards_title h2{
    font-size:33px;
    font-weight:lighter;
    padding-top:60px;
    color : #fff;
}

.awards_background_img_layout{
    position: absolute;
    top:0;
    z-index: -1;
    
}

.awards_background_img_container{
    display:flex;
}

.awards_background_img{
    flex-shrink : 0;
    width:100%;
    height:940px;
    overflow: hidden;
}

.awards_background_img img{
    width:100%;
    transform: scale(120%);
    transform: translateY(-100px);
    filter:brightness(30%);
}

.award_img_layout{
    position: absolute;
    top:140px;
    left:33.2%;
}

.award_img_container{
    display: flex;
}

.award_img{
    max-width: 660px;
    max-height: 719px;
    background-color: #111;
    margin:0px 100px 0px 100px;
}

.award_img:first-child{
    margin-left:0px;
}

.award_img_text{
    padding:40px;
    color: #fff;
}

.award_img_text h3{
    font-size:15.5px;
    font-weight: lighter;
}

.award_img_text h2{
    font-size:22px;
    font-weight: lighter;
    margin:15px 0px 30px 0px;
}

.award_img_text p{
    font-size:14px;
    color: #ccc;
}

.award_more{
    display:flex;
    justify-content: space-between;
    align-items: center;
    margin:0px 40px 40px 40px;
}

.award_more img{
    width:28%;
}

.award_more div{
    display:flex;
    align-items: center;
    padding-top:50px;
}

.award_more div a{
    font-size:16px;
    margin-right:5px;
    border-bottom: 1px solid #fff;
    color:#fff;
    cursor: pointer;
}

.award_more div svg{
    font-size:16px;
    color:#fff;
}

.award_btn{
    position: absolute;
    bottom:3%;
    left:50%;
    transform: translateX(-50%);
}
.award_btn button{
    width:50px;
    height: 3.5px;
    margin:0px 5px 0px 5px;
    border: none;
    background-color: #6d6d6d;
    cursor: pointer;
}

.award_btn button:first-child{
    background-color: #fff;
}

.space_road_layout{
    display:flex;
    justify-content: space-between;
    padding:250px 0px 250px 0px;
    background-color: #111111;
}

.space_container{
    position: relative;
    width:49%;
}

.space_container img{
    width:100%;
    filter: brightness(50%);
}

.space_text{
    position: absolute;
    top:10%;
    left:7%;
    color:#fff;
}

.road_container{
    position: relative;
    width:49%;
}

.road_container img{
    width:100%;
    filter: brightness(50%);
}

.road_text{
    position: absolute;
    top:10%;
    left:7%;
    color:#fff;
}

.space_road_layout h2{
    font-size:40px;
    font-weight: lighter;
}

.space_road_layout p{
    margin:20px 0px 40px 0px;
}

.space_btn{
    display:flex;
    align-items: center;
    font-size:17px;
    cursor: pointer;
}

.space_btn a{
    border-bottom: 1px solid #fff;
    padding-bottom:3px;
    margin-right:5px;
}

.road_btn{
    display:flex;
    align-items: center;
    font-size:17px;
    cursor: pointer;
}

.road_btn a{
    border-bottom: 1px solid #fff;
    padding-bottom:3px;
    margin-right:5px;
}

.event_container{
    position: relative;
    padding:0px 0px 150px 0px;
    background-color: #111111;
}

.event_container img{
    width:100%;
}

.event_text{
    position: absolute;
    top:30%;
    left:7%;
    color:#fff;
}

.event_text h2{
    font-size:50px;
    font-weight:lighter;
}

.event_text p{
    font-size:26px;
    margin:50px 0px 50px 0px;
}

.event_text button{
    font-size:16px;
    padding:18px 70px 18px 70px;
    border: 1px solid #fff;
    background-color: transparent;
    color:#fff;
    cursor: pointer;
}