.footer_share{
    background-color: #111111;
    border-bottom: 1px solid #1e1e1e;
}

.footer_share_container{
    display:flex;
    justify-content: space-between;
    width:85%;
    margin:auto;
    padding:20px 0px 20px 0px;
}

.share_text{
    font-size:15px;
    color:#fff;
}

.share_ul{
    display:flex;
    width:90%;
    color:#bbb;
}

.share_ul li{
    font-size:17px;
    padding:0px 30px 0px 0px;
}



.share_top{
    display:flex;
    align-items: center;
    color:#fff;
    cursor: pointer;
}

.share_top span{
    margin-right:12px;
}

.footer_notice{
    display:flex;
    justify-content: space-between;
    width:85%;
    height:19px;
    margin:auto;
    padding:20px 0px 30px 0px;
    color:#fff;
    overflow: hidden;
}
.footer_notice ul {
    transform: translateY(-25px);
}

.footer_notice ul li{
    padding:30px 0px 30px 0px;
}

.footer_notice svg{
    padding-top:5px;
    cursor: pointer;
}

.notice_play{
    display: none;
    position: absolute;
    right:7.4%;
}

.Footer_href{
    padding:100px 0px 50px 0px;
    background-color: #151515;
}

.Footer_container{
    display:flex;
    width:85%;
    margin:auto;
}

.Footer_item{
    width:30%;
    padding-bottom:50px;
}

.Footer_item h2{
    color:#fff;
    font-size:16px;
    padding-bottom:20px;
}

.Footer_item p{
    font-size:13.5px;
    padding-bottom:20px;
    color:#bbb;
    cursor: pointer;
}

.footer_about{
    background-color: #111111;
    border-bottom: 1px solid #1e1e1e;
}

.footer_about ul{
    display:flex;
    width:85%;
    margin: auto;
    padding:40px 0px 40px 0px;
}

.footer_about ul li {
    font-size:15px;
    padding:0px 20px 0px 20px;
    color:#bbb;
    border-left:1px solid #1e1e1e;
}

.footer_about ul li:first-child{
    padding-left:0px;
    border:none;
}

.footer_about ul li:last-child{
    color:#c36f51;
}

.footer_more{
    background-color: #111111;
    border-bottom: 1px solid #1e1e1e;
}

.footer_more ul{
    display:flex;
    width:85%;
    margin: auto;
    padding:45px 0px 45px 0px;
}

.footer_more ul li{
    font-size:15px;
    font-weight: bold;
    padding:0px 40px 0px 40px;
    color:#bbb;
}

.footer_more ul li:first-child{
    padding-left:0px;
}

.footer_copy{
    position: relative;
    background-color: #111111;
}

.footer_copy div{
    display:flex;
    flex-direction: column;
    align-items: center;
    padding:80px 0px 80px 0px;
}

.footer_copy div p{
    text-align: center;
    color:#fff;
}

.footer_mark{
    position: absolute;
    top:10%;
    right:8%;
}
