.nav_bar_res{
    position: fixed;
    width:100%;
    display:flex;
    justify-content: space-between;
    align-items: center;
    padding:20px 0px 20px 0px;
    color:#fff;
    background-color: #111111;
    z-index:999;
}

.logo_res{
    width:23%;
    margin-left:20px;
}

.logo_res img{
    width:100%;
}

.sub_nav_res ul{
    display:flex;
    margin-right:20px;
}

.sub_nav_res ul li{
    font-size:20px;
    margin-left:20px;
}

.img_banner_res{
    position: relative;
    overflow: hidden;
}

.img_container_res{
    display:flex;
}

.img_item_res{
    position: relative;
    width:100%;
    flex-shrink: 0;
    color:#fff;
}

.img_item_res img{
    width:100%;
    height: 100%;
}

.img_text_header_res{
    position: absolute;
    top:15%;
    left:7%;
}

.img_text_header_res h3{
    font-size:15px;
    font-weight:lighter;
    margin-bottom:5px;
}

.img_text_header_res h1{
    font-size:37px;
    font-weight:lighter;
    margin-bottom:5px;
}

.img_text_header_res h2{
    font-weight: lighter;
}

.img_text_p_res{
    position: absolute;
    bottom:18%;
    left:7%;
    display:flex;
    flex-direction: column;
}

.img_text_p_res_row{
    position: absolute;
    bottom:18%;
    left:7%;
    display:flex;
    align-items: center;
}

.img_text_p_res_row p{
    font-size:15px;
    font-weight: bold;
    margin-right:5px;
}

.img_text_p_res div{
    display:flex;
    align-items: center;
    margin-top:25px;
}

.img_text_p_res div p{
    font-size:15px;
    font-weight: bold;
    margin-right:5px;
}

.img_btn_container_res{
    position: absolute;
    width:100%;
    bottom: 10%;
    text-align: center;
}

.img_btn_container_res button{
    width:8px;
    height:8px;
    padding:0px;
    margin:0px 8px 0px 8px;
    border-radius: 50%;
    border:none;
    background-color: #808388;
    cursor: pointer;
}

.img_btn_container_res button:first-child{
    outline: 2px solid #fff;
    background-color: transparent;
}
