.awards_slide_res{
    position:relative;
    height:830px;
}

.awards_title_res{
    width: 85%;
    margin:auto;
}

.awards_title_res h2{
    font-size:27px;
    font-weight:lighter;
    padding-top:50px;
    color : #fff;
}

.awards_background_img_layout_res{
    position: absolute;
    top:0;
    z-index: -1;
    overflow: hidden;
}

.awards_background_img_container_res{
    display:flex;
}

.awards_background_img_res{
    flex-shrink : 0;
    width:100%;
    height:830px;
    overflow: hidden;
}

.awards_background_img_res img{
    width:100%;
    height:770px;
    transform: scale(130%);
    filter:brightness(40%);
}

.award_img_layout_res{
    position: absolute;
    top:140px;
    overflow: hidden;

}

.award_img_container_res{
    display:flex;
}

.award_img_res{
    width:100%;
    flex-shrink: 0;
}

.award_img_container_children{
    width:85%;
    height:50%;
    margin: auto;
}

.award_img_container_children img{
    width:100%;
    height:100%;
}

.award_img_text_res{
    width:85%;
    margin: auto;
    overflow: hidden;
    color:#fff;
    background-color: #111;
}

.award_img_text_res h3{
    font-size:13px;
    font-weight: lighter;
    padding:20px 8px 20px 8px;
}

.award_img_text_res h2{
    height:50px;
    font-size:18px;
    font-weight: lighter;
    padding:0px 8px 20px 8px;
}

.award_img_text_res p{
    font-size:13px;
    color: #ccc;
    padding:0px 8px 20px 8px;
}


.award_more_res{
    display:flex;
    justify-content: space-between;
    align-items: center;
    width: 85%;
    margin: auto;
    color:#fff;
    background-color: #111;
    padding-bottom:30px;
}

.award_more_res img{
    width:28%;
    margin: none;
    padding-left:10px;
}

.award_more_res div{
    display:flex;
    align-items: center;
    padding-right:10px;
}

.award_more_res div a{
    font-size:14px;
    margin-right:5px;
    padding-bottom:3px;
    border-bottom: 1px solid #fff;
    color:#fff;
    cursor: pointer;
}

.award_btn_res{
    position: absolute;
    width: 100%;
    bottom:5.5%;
    text-align: center;
}

.award_btn_res button{
    width:40px;
    height: 2.5px;
    margin:0px 5px 0px 5px;
    border: none;
    background-color: #6d6d6d;
    cursor: pointer;
}

.award_btn_res button:first-child{
    background-color: #fff;
}

.space_road_layout_res{
    padding-top:100px;
    padding-bottom: 100px;
    display:flex;
    flex-direction: column;
    background-color: #111111;
}

.space_container_res{
    position: relative;
}

.space_container_res img{
    width:100%;
    filter: brightness(50%);
}

.space_text_res{
    position: absolute;
    top:5%;
    left:7%;
}

.space_text_res h2{
    font-size:30px;
    font-weight: lighter;
    color:#fff;
}

.space_text_res p{
    font-size:14px;
    color:#fff;
    margin:20px 0px 20px 0px;
}

.space_btn_res{
    display:flex;
    align-items: center;
    color:#fff;
}

.space_btn_res a{
    font-size:14px;
    margin-right:5px;
    padding-bottom:5px;
    border-bottom: 1px solid #fff;
}

.road_container_res{
    position: relative;
    padding-top:30px;
}

.road_container_res img{
    width:100%;
    filter: brightness(50%);
}

.road_text_res{
    position: absolute;
    top:10%;
    left:7%;
}

.road_text_res h2{
    font-size:30px;
    font-weight: lighter;
    color:#fff;
}

.road_text_res p{
    font-size:14px;
    color:#fff;
    margin:20px 0px 20px 0px;
}

.road_btn_res{
    display:flex;
    align-items: center;
    color:#fff;
}

.road_btn_res a{
    font-size:14px;
    margin-right:5px;
    padding-bottom:5px;
    border-bottom: 1px solid #fff;
}

.event_container_res{
    position:relative;
    padding-bottom:120px;
    background-color: #111111;
}

.event_container_res img{
    width:100%;
    filter: brightness(70%);
}

.event_text_res{
    position: absolute;
    top:30%;
    left:10%;
}

.event_text_res h2{
    font-size:32px;
    font-weight: lighter;
    color: #fff;
}

.event_text_res p{
    font-size:25px;
    color:#fff;
    margin:30px 0px 30px 0px;
}

.event_text_res button{
    width:100%;
    font-size:16px;
    padding:15px 0px 15px 0px;
    color: #fff;
    background-color: transparent;
    border: 1px solid #fff;
}