.ourmodels{
    padding-top:200px;
    background-color: #111111;
}

.ourmodels_header{
    width:70%;
    margin:auto;
}

.ourmodels_header h2{
    font-size:38px;
    font-weight: lighter;
    color:#fff;
}

.ourmodels_header p{
    font-size:16px;
    font-weight: bold;
    color:#bebebe;
    margin:30px 0px 30px 0px;
}

.ourmodels_header button{
    position: relative;
    display:flex;
    justify-content: space-between;
    width:23%;
    padding:10px 5px 10px 5px;
    border: none;
    border-bottom: 2px solid #fff;
    font-size:15px;
    color: #fff;
    background-color: transparent;
    cursor: pointer;
}

.hidden_list{
    position: absolute;
    bottom:0px;
    left:0px;
    display:flex;
    flex-direction: column;
    text-align: left;
    width: 100%;
    height:0px;
    overflow: hidden;
    z-index: 9;
}

.hidden_list li{
    padding:15px 10px 15px 10px;
    font-size:14px;
    color:#757575;
    border-bottom: 1px solid #757575;
    background-color: #111111;
}

.hidden_list li:last-child{
    border:none;
}

.hidden_list li:hover{
    transition: all 0.3s;
    color: #fff;
}

.ourmodels_img_layout{
    width:70%;
    margin:auto;
    padding:100px 0px 40px 0px;
    overflow:hidden;
}

.ourmodels_img_container{
    display:flex;
    width:100%;
}

.ourmodes_img_item{
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    flex-shrink : 0;
    width:25%;
    padding:40px;
    margin:0px 20px 0px 20px;
    background-color: rgb(21, 21, 21);
}

.ourmodes_img_item:first-child{
   margin-left:0px;
}

.ourmodes_img_item h3{
    font-size:30px;
    font-weight: lighter;
    padding-bottom:10px;
    color:#bbb;
}

.ourmodes_img_item p{
    font-size:15px;
    color:#bbb;
}

.ourmodes_img_item img{
    width:100%;
    padding:40px 0px 40px 0px;
}

.ourmodes_img_item button{
    width:100%;
    padding:15px 0px 15px 0px;
    font-size:14px;
    font-weight: bold;
    border: 1px solid #fff;
    color:#bbb;
    background-color: transparent;
    cursor: pointer;
}


.ourmodels_btn{
    width:70%;
    margin:auto;
    display:flex;
    justify-content: center;
    align-items: center;
    padding-bottom:100px;
}

.ourmodels_btn button{
    width:10px;
    height:10px;
    padding:0px;
    margin:0px 10px 0px 10px;
    border-radius: 50%;
    border:none;
    background-color: #808388;
    cursor: pointer;
}

.ourmodels_btn button:first-child{
    border:2px solid #fff;
    background-color: transparent;
}

.ourmodels_icon{
    display:flex;
    justify-content: space-around;
    align-items: center;
    width:70%;
    margin:auto;
    padding:150px 0px 200px 0px;
}

.ourmodels_icon_item{
    display:flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
}

.ourmodels_icon_item p{
    font-size: 16px;
    padding:20px 0px 0px 0px;
    color : #fff;
}