.content{
    padding:150px 0px 150px 0px;
}

.content h2{
    font-size:30px;
    text-align: center;
    padding-bottom:50px;
}

.content_container{
    display:flex;
    justify-content: space-evenly;
    width:90%;
    margin: auto;
}

.content_item{
    width:30%;
}

.content_item img{
    width:100%;
}

.content_item h5{
    margin-top: 20px;
    font-size:18px;
}

.content_item p{
    font-size:15.5px;
    margin:20px 0px 20px 0px;
}

.content_item div{
    display: flex;
    align-items: center;
    cursor: pointer;
}

.content_item div a{
    border-bottom: 1px solid black;
    padding-bottom:3px;
    margin-right:5px;
}